@import "../../../../../Mixins.scss";


.MgtStaffOverview {
  & .TopActions {
    height: 40px;
    margin-bottom: 20px;
    font-weight: 900;
    & h6 {
     color: $body-text-color;
     font-size: 18px;
    }
    & button {
     color: $pri-color;
     border-color: $pri-color;
     margin-right: 15px;
    }
    & svg {
      margin-right: 3px;
    }
  }
 }

.StaffImgAlt {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 40px;
  color: white;
  font-size: 20px;
  font-weight: bold;
  border-radius: 28px;
}

.StaffTable {
  & td {
      font-weight: bold;
      font-size: 12px;
      color: $body-text-color;
      text-align: left;
  }
}
